.screen {
    padding: 35px 0 45px;

    @include min(md) {
        padding: 45px 0 60px;
    }

    &__inner {
        @include flexCenter;

        @include xs-block() {
            flex-direction: column;
        }
    } //.screen__inner

    &__image {
        flex-basis: 40%;

        .mockup-slider {
            width: 80%;
            margin: auto;
        }
    } //.screen__image

    &__text {
        flex-basis: 60%;
        // align-items: center;
        // justify-content: center;
        // @include flexColumn;

        // .text-block {
        // }
    } //.screen__text

    &__title {
        // position: relative;
        margin-bottom: 30px;
        color: $gray--dark;
        font-size: 30px;
        font-weight: 500;
        line-height: 1.3;
        text-align: center;

        @include min(xs) {
            // margin-bottom: 35px;
            margin-bottom: 50px;
        }

        @include min(sm) {
            font-size: 35px;
            line-height: 1.5;
        }

        &.mb-20 {
            margin-bottom: 20px;
        }

        &--white {
            color: #fff;
        } //.screen__title--white
    } //.screen__title

    &__subtitle {
        max-width: 60%;
        margin: auto;
        margin-bottom: 30px;
        font-size: 22px;
        font-weight: 300;
        text-align: center;

        @include md-block() {
            font-size: 18px;
        }

        @include xs-block() {
            max-width: 100%;
        }
    } //.screen__subtitle

    &__desc {
        margin-bottom: 40px;
        color: $color-text;

        font-size: 16px;
        line-height: 1.7;
        text-align: center;

        @include min(md) {
            font-size: 20px;
        }

        p + p {
            margin-top: 10px;
        }

        &.mb-15 {
            margin-bottom: 15px;
            line-height: 1;

            @include sm-block() {
                line-height: 1.4;
            }
        }
    } //.screen__description

    &__icon-slider {
        position: relative;

        @include screen(768px, 1024px) {
            max-width: 70%;
            margin: auto auto 20px;
        }

        @include min(md) {
            order: 1;
            width: 350px;
            margin: 0;
            margin-right: auto;
        }

        .bb-slider__slide + .bb-slider__slide {
            @include min(md) {
                margin-top: 20px;
            }
        }
    } //.screen__icon-slider

    &__img {
        width: 80%;
        margin: auto;

        &--small {
            width: 100%;
            max-width: 230px;
        } //.screen__img--small
    } //.screen__img

    &__btn {
        display: block;
        width: 100%;
        margin-top: 40px;
        font-size: 17px;

        @include min(xs) {
            max-width: 320px;
            margin: 30px auto 0;
        }
    } //.screen__btn

    &__form {
        &--right {
            margin-left: auto;
        } //.screen__form--right

        &--center {
            .form {
                margin: auto;
                margin: auto;
            }
        } //.screen__form--center
    } //.screen__form

    &--about {
        @include xs-block() {
            padding-bottom: 0;
        }
    }

    &--img {
        @include sectionOverlay;
        @include cover;

        & > * {
            z-index: 1;
            color: #fff;
        }

        .screen {
            &__title,
            &__desc {
                color: #fff;
            }
        }

        &.no-overlay {
            &::before {
                background-color: transparent;
            }
        }

        .btn-default {
            &:not(.form__btn):not(.float-card__btn) {
                box-shadow: 0 2px 5px 0 $gray--dark;
            }
        }
    } //.screen--img

    &--get-icons {
        .container {
            @include min(md) {
                max-width: 900px;
            }
        }

        .screen__inner {
            margin-bottom: 20px;

            @include min(md) {
                display: flex;
                align-items: center;
                margin-bottom: 60px;
            }
        }

        .list--square {
            margin-bottom: 50px;
            @include min(md) {
                margin-right: 60px;
                margin-bottom: 0;
            }
        }
    } //.screen--get-icons

    &--icons {
        .screen__slider {
            @include min(xs) {
                width: 100%;
                max-width: none;
            }
        }
    } //.screen--icon-collect

    &--adv {
        .text-block {
            &__wrap {
                @include min(md) {
                    flex-direction: row;
                    align-items: baseline;
                }
            }
        }

        img {
            @include min(md) {
                width: 80%;
                margin: auto;
            }
        }

        .screen__img {
            @include min(md) {
                display: block;
                width: 100%;
            }

            img {
                width: 80%;
                margin: auto;
            }
        }
    } //.screen--advantages

    &--reg {
        padding: 15px 0;

        @include xs-block() {
            padding-bottom: 30px;
        }

        @include min(md) {
            display: none;
        }
    } //.screen--reg

    &--reviews {
        .bb-slider__slide {
            @include min(md) {
                padding: 0 30px;
            }
        }
    } //.screen--reviews

    &--site {
        @include xs-block() {
            padding-top: 0;
        }

        .container {
            @include min(md) {
                width: 1000px;
            }
        } //.screen--site .container

        .screen {
        } //.screen--site .screen

        .text-block {
            &__sub {
                @include min(md) {
                    max-width: 320px;
                }
            } //.screen--site .text-block__sub
        } //.screen--site .text-block
    } //.screen--site

    &--why-we {
        .text-block {
            flex-wrap: wrap;
            align-items: flex-start;

            @include min(md) {
                margin-bottom: 0;
            }

            &__item {
                flex-basis: 25%;
                margin-bottom: 50px;
            }
        }
    } //.screen--why-we

    &--course {
        .icon-list {
            &__icon {
                border: 1px solid #fff;

                @include min(md) {
                    border: 2px solid #fff;
                }
            } //.screen--course .icon-list__icon

            &__sub {
                font-size: 16px;
            } //.screen--course .icon-list__sub

            &__item {
                @include col;
                @include size(2);
                @include size-sm(3);
                @include size-xs(12);
            }
        } //.screen--course .icon-list
    } //.screen--course

    &--speaker {
        .float-card {
            &__title {
                margin-bottom: 5px;
                font-size: 20px;
                font-weight: 600;
                // border-bottom: 1px solid $color-border--lighten;
            } //.screen--speaker .float-card__title

            &__sub {
                display: block;
                text-align: center;
            }

            &__desc {
                padding-top: 10px;
                @include border(top);

                p + p {
                    margin-top: 10px;
                }
            } //.screen--speaker .float-card__sub
        } //.screen--speaker .float-card
    } //.screen--speaker

    &--sale {
        .btn-link {
            width: 100%;
            text-align: center;
        }
    } //.screen--sale

    &--mockup {
        // position: relative;
        z-index: 100;
        justify-content: center;
        padding: 0;
        @include flex;

        .video-mockup {
            @include min(md) {
                margin: -180px auto 0;
            }
        }
    } //.screen--mockup

    &.bg-gray {
        background: $color-bg--lightest;
    } //.screen.bg-grey

    .list {
        &--pregress {
            margin-bottom: 30px;
        }
    } //.list

    .form {
        width: 100%;
        // margin: 0;
        // margin-left: auto;
        border-radius: $border-radius;
        background-color: rgba(255, 255, 255, 0.75);
        color: $color-text--darkest;

        @include sm-block() {
            padding: 10px 10px 20px;
        }

        @include xs-block() {
            // width: 100%;
            // max-width: fit-content;
            width: auto;
            max-width: 100%;
            margin: 0 10px;
            padding-top: 0;
        }

        // @include i5-block() {
        //     max-width: fit-content;
        //     margin: 0 10px;
        // }

        &__title {
            // width: 280px;
            margin-top: 15px;
            color: $gray--dark;
            font-size: 24px;
            font-weight: 600;
            line-height: 1.2;

            @include i7-block() {
                width: 100%;
            }
        } //.screen .form__title

        &--center {
            margin: 0 auto;

            @include sm-block() {
                padding: 10px 10px 20px;
            }

            @include xs-block() {
                width: 100%;
                margin: 0;
            }
        } //.screen .form--center

        .bb-checkbox__box {
            border-color: $color-border;
        }

        .bb-checkbox--radio .bb-checkbox__title {
            font-size: 14px;
        }

        .bb-checkbox:hover .bb-checkbox__box {
            @include min(md) {
                border-color: $gray--dark;
            }
        }

        .bb-title--devider::after,
        .bb-title--devider::before {
            border-top: 1px solid $gray--light;
        }
    } //.form

    .button-box {
        .btn {
            min-width: 200px;
            @include xs-block() {
                width: 100%;

                & + .btn {
                    margin-top: 20px;
                    margin-left: 0;
                }
            }

            & + .btn {
                margin-left: 15px;
            }
        }
    } //.screen .buttons-box

    .slick-dots {
        bottom: -24px;
    } //.screen .slick-dots
} //.screen

.video {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;

    @include min(md) {
        margin-left: 0;
    }

    iframe {
        @include coverdiv;
    }
} //.video
