.page-onepage {
    &:not(.page-onepage--about) {
        .card {
            &__content {
                background-color: transparent;
            }
        }
    }

    .wrapper {
        &__inner {
            z-index: initial;
        }
    } //.page-onepage .wrapper

    .header {
        &__inner {
            height: 50px;
            border: 0;
            @include flexCenter;
        } //.header__inner

        &__logo {
            margin-top: -4px;

            .logo__link {
                font-size: 15px;
                font-weight: 500;
            }
        } //.header__logo
    } //.page-onepage .header

    .header-user {
        height: 50px;
    } //.page-onepage .header-user

    .logo {
        display: inline-block;
        flex-shrink: 0;
        margin-top: -2px;

        @include sm-block() {
            position: relative;
            top: 0;
            flex: 1;
            margin-top: 0;
        }

        &__img {
            width: 130px;
        } //.logo__img
    } //.page-onepage .logo

    .nav {
        margin-left: auto;

        &__link {
            line-height: 50px;
        }
    } //.page-onepage .nav

    .nav-dropdown {
        top: 50px;
    } //.page-onepage .nav-dropdown

    .toggle-menu {
        display: flex;
    } //.toggle-mnu

    &--home {
        .onepage {
            padding-bottom: 30px;

            @include sm-block() {
                background-color: #fff;
            }
        }

        .logo {
            &__link {
                color: #fff;
            }
        }

        .toggle-menu {
            &__icon {
                background-color: #fff;
            }
        }

        .page-title {
            @include sm-block() {
                width: 100%;
                margin: 0;
                font-size: 16px;
                text-align: center;
            }

            &__wrap {
                @include sm-block() {
                    margin-bottom: 0;
                    background-color: $color-bg--lighten;
                }
            }

            &--link {
                @include sm-block() {
                    padding: $gutter 0;
                }
            }
        } //.onepage--home .page-title

        .footer {
            padding: 10px 0 13px;

            @include sm-block() {
                background-color: $color-bg--lighten;
            }
        }
    } //.onepage--home

    &--about,
    &--reviews {
        @include sm-block() {
            .card-info {
                &__user {
                    @include sm-block() {
                        display: none;
                    }
                }

                &__social {
                    @include sm-block() {
                        display: none;
                    }
                }
            }
        }

        .onepage {
            padding-top: 50px;
        }

        .footer {
            padding: 10px 0 13px;
        }
    }

    &--about {
        @include min(md) {
            .toggle-mnu {
                margin-left: -4px;
            }
        }

        @include sm-block() {
            .page-title {
                text-align: left;

                &__wrap {
                    align-items: flex-start;
                    margin: 0 0 15px;
                    padding: 0;
                    background-color: transparent;
                }
            }

            .card {
                &__content {
                    margin-top: 10px;
                }
            }

            .footer {
                background-color: #fff;
            }
        }

        .onepage {
            @include sm-block() {
                background-color: #e0e0e0;
            }

            .card {
                &__content {
                    margin-top: 0;
                }
            }

            & > .container {
                padding: 0 8px;

                @include sm-block() {
                    padding: 0;
                }
            }
        }
    } //.onepage--about

    &--registration {
        .screen {
            .container {
                @include max(xs) {
                    padding: 0 20px;
                }
            }
        }

        .form {
            padding: 0;

            @include min(xs) {
                padding: 15px 30px 30px;
            }
        }

        .footer {
            padding: 0;
        }
    } //.onepage--registratio
} //.onepage

.onepage {
    background-color: #fff;

    &__title {
        display: block;
        margin-top: 100px;
        margin-bottom: 30px;

        font-weight: 400;
    } //.onepage__title

    &__gallary {
        margin-bottom: $gutter;
        padding-bottom: 5px;
        background-color: #fff;

        @include sm-block() {
            margin-bottom: 10px;

            .bb-slider {
                &__arrow {
                    display: none !important;
                }
            }

            .slick-slide {
                margin: 0;
            }
        }
    } //.onepage__gallary

    &__social {
        display: none;

        @include max(xs) {
            display: block;
        }
    } //.onepage__social

    .page-title {
        @include sm-block() {
            margin: 0;
            font-size: 16px;
        }
    } //.onepage .page-title

    .user {
        @include flexCenter;

        @include sm-block() {
            margin-bottom: 0;
            padding-bottom: 0;
            border: 0;
        }

        &__avatar {
            margin-right: 15px;
            margin-bottom: 0;
        } //.onepage .user__avatar

        &__img {
            width: 50px;
            height: 50px;
        }

        &__name {
            color: $gray--darkest;
            font-size: 16px;
            font-weight: 500;
            line-height: 1.2;
            @include max(xs) {
                font-size: 15px;
            }
        }
    } //.onepage .user

    .card {
        &__content {
            margin-top: 25px;
            // background-color: transparent;

            @include min(md) {
                margin-top: 55px;
            }

            .bb-accordeon__item {
                margin-top: 0;

                @include min(md) {
                    margin-bottom: 10px;
                }

                @include sm-block() {
                    border-top: 0;
                    border-right: 0;
                    border-left: 0;
                }
            } //.onepage .card__content .bb-accordeon__item
        }

        &__about {
            @include sm-block() {
                // margin-bottom: $gutter;
                // padding: 0;
                // box-shadow: none;
                // background-color: transparent;
            }
        }

        &__about_text {
            // padding: 10px 15px;
            //@include box;

            @include max(xs) {
                // margin: 0 -10px;
                // @include border(top);
                // @include border(bottom);
                // box-shadow: none;
            }
        } //.onepage .card__about_text

        &-services {
            @include sm-block() {
                margin-bottom: 1px;
            }
        }
    } //.onepage .card

    .card-info {
        top: 0;

        &__top {
            padding: 0;
            overflow: initial;
            border: 0;
            border-radius: 0;
            background-color: transparent;
            box-shadow: none;

            &.is-booked {
                .card-info__btn {
                    @include tY(0);
                }
            }
        } //.onepage .card-info__top

        &__bg {
            // margin: 2px;
            padding: 15px;
            @include box;
            @include sm-block() {
                margin: 0;
                padding: 0;
                box-shadow: none;
            }
        } //.onepage .card-info__bg

        &__category {
            margin-bottom: 13px;
            padding-top: $gutter;
            border-top: 1px solid $color-border--lightest;

            @include sm-block() {
                border-top: 0;
            }
        } //.onepage .card-info__category

        &__social {
            width: 100%;
            margin: auto;
        } //.onepage .card-info__social

        &__btn {
            @include sm-block() {
                display: block;
                padding: 0;
                // border-radius: $border-radius;
                // width: 90%;
                // max-width: 414px;
                // position: fixed;
                // bottom: 65px;
                // left: 50%;
                // @include tX(-50%);
                // transform: translate(-50%, 200%);
                @include tY(100%);
                @include tr;
            }
        } //.onepage .card-info__btn
    } //.onepage .card-info

    .card-adress {
        @include sm-block() {
            // &__inner {
            //     padding: 0;
            //     box-shadow: $bxs-box;
            //     background-color: #fff;

            //     .place-text {

            //         &__wrap {
            //             padding: 13px 15px 10px;

            //             & + .place-text__wrap {
            //                 border-top: 1px solid $color-border--lightest;
            //             }
            //         }

            //         &__box {
            //             margin-bottom: 0;
            //         }
            //     }
            // }

            // .working__list {
            //     box-shadow: $bxs-box;
            //     border: 0;
            // }
        }

        &__place {
            @include sm-block() {
                margin-bottom: 10px;
            }
        }

        &__map {
            @include sm-block() {
                box-shadow: none;
            }
        }
    } //.onepage .card-adress__working

    .card-request-form {
        &__top {
            margin-top: 0;
        }
    } //.onepage .card-request-form

    .social {
        &__inner {
            justify-content: center;
            @include flexCenter;
        }

        &__devider {
            max-width: 100px;
            margin: $gutter / 1.5 auto;
        }

        .bb-title--devider {
            width: 100%;
            margin: 30px auto;

            @include sm-block() {
                margin: 20px 0;
            }

            &::before,
            &::after {
                width: 38%;
            }
        } //.onepage .social .bb-title--devider
    } //.onepage .social

    .form {
        &__social {
            .btn-social {
                padding: 0 12.5px;
            }
        }
    } //.onepage .form

    .button-social,
    .btn-social {
        padding: 10px;

        @include sm-block() {
            &::before {
                font-size: 20px;
            }

            &-text {
                padding: 10px 0;
            }
        }

        & + .button-social {
            margin-left: $gutter / 2;
        } //.onepage .button-social + .button-social

        & + .btn-social {
            margin-left: $gutter / 2;
        } //.onepage .btn-social + .btn-social
    } //.onepage .btn-social

    .bb-accordeon {
        &__item,
        &__title {
            @include sm-block() {
                border-radius: 0;
                box-shadow: none;
            }
        }

        &__title {
            border-bottom: 1px solid $color-border--lightest;
        }
    } //.onepage .bb-accordeon

    &--beautylink {
        .section {
            &--small-offset {
                &:first-of-type {
                    padding-top: 0;
                }

                .section {
                    &__img {
                        @include xs-block() {
                            width: 75%;
                        }
                    }
                }
            }

            &--skew-box {
                @include min(xs) {
                    background-color: transparent;

                    &::before,
                    &::after {
                        display: none;
                    }
                }
            }
        }

        .inform-block {
            &__title {
                @include xs-block() {
                    font-size: 26px;
                }
            }
        }

        .form {
            &--horizontal {
                .form-success {
                    @include min(xs) {
                        background-color: $color-bg--lighten;
                    }
                }
            }
        }
    } //.onepage--beautylink
} //.onepage

.icon-list {
    @include row-flex;

    &__item {
        position: relative;
        margin-bottom: 30px;
        @include flexCenter;

        @include min(xs) {
            flex-direction: column;
        }

        &:last-child {
            margin-bottom: 0;
        }
    } //.icon-list__item

    &__icon {
        width: 50px;
        height: 50px;
        margin-right: 20px;
        border: 2px solid #fff;
        border-radius: 50%;
        @include flexCenter;

        @include min(xs) {
            display: flex;
            justify-content: center;
            width: 70px;
            height: 70px;
            margin-right: 0;
            margin-bottom: 20px;
        }

        @include min(md) {
            margin-bottom: 20px;
        }

        i {
            margin: auto;
            font-size: 20px;

            @include min(sm) {
                font-size: 30px;
            }
        }
    } //.icon-list__icon

    &__svg {
        width: 50px;
        height: 60%;
        fill: #fff;

        @include min(xs) {
            width: 70px;
        }
    } //.icon-list__svg

    &__title {
        margin-bottom: 15px;
        font-size: 20px;
        font-weight: 500;
        line-height: 1.3;

        @include xs-block() {
            margin: 5px auto;
        }

        @include min(xs) {
            font-size: 25px;
            text-align: center;
        }

        @include min(sm) {
            font-size: 30px;
        }

        @include min(md) {
            font-size: 35px;
        }
    } //.icon-list__title

    &__sub {
        font-size: 16px;
        font-weight: 300;

        @include min(xs) {
            font-size: 18px;
            text-align: center;
        }

        @include min(sm) {
            font-size: 22px;
        }
    } //.icon-list__sub

    &--white {
        color: #fff;
    } //.icon-list--white

    &--column {
        flex-direction: row;
        align-items: flex-start;
        justify-content: center;

        @include sm-block() {
            flex-direction: column;
            align-items: center;
        }

        .icon-list {
            &__item {
                flex-direction: column;
            } //.icon-list--column .icon-list__item

            &__icon {
                justify-content: center;
                margin-right: 0;
                margin-bottom: 20px;
                @include flexCenter;

                img {
                    width: auto;
                    height: 100%;
                }
            } //.icon-list--column .icon-list__icon

            &__title {
                display: flex;
                align-items: center;
                justify-content: center;
                max-width: 20ch;
                font-size: 25px;
                text-align: center;

                @include md-block() {
                    font-size: 20px;
                }
            } //.icon-list--column .icon-list__title

            &__desc {
                flex-direction: column;
                justify-content: center;
                @include flexCenter;
            } //.icon-list--column .icon-list__desc

            &__sub {
                text-align: center;
            } //.icon-list--column .icon-list__sub
        } //.icon-list--column .icon-list
    } //.icon-list--column

    &--item-row {
        align-items: flex-start;
        justify-content: flex-start;

        .icon-list {
            &__item {
                flex-direction: row;
            } //.icon-list--column .icon-list__item

            &__icon {
                justify-content: center;
                margin-right: 0;
                margin-bottom: 20px;
                @include flexCenter;

                img {
                    width: auto;
                    height: 100%;
                }
            } //.icon-list--column .icon-list__icon

            &__title {
                display: flex;
                align-items: center;
                justify-content: center;
                max-width: 20ch;
                font-size: 25px;
                text-align: center;

                @include md-block() {
                    font-size: 20px;
                }
            } //.icon-list--column .icon-list__title

            &__desc {
                flex-direction: column;
                justify-content: center;
                @include flexCenter;
            } //.icon-list--column .icon-list__desc

            &__sub {
                text-align: center;
            } //.icon-list--column .icon-list__sub
        } //.icon-list--column .icon-list
    } //.icon-list--item-row
} //.icon-list

.float-card {
    position: relative;
    width: 80%;
    max-width: 375px;
    margin: auto;
    padding: 25px;
    border-radius: $border-radius;
    background-color: #fff;

    &__sub {
        margin-bottom: 10px;
        color: $color-text--light;
    } //.float-card__sub

    &__img {
        justify-content: center;
        max-width: 200px;
        margin: 0 auto 20px;
        @include flexCenter;

        &--rounded {
            img {
                border-radius: 50%;
            }
        } //.tab__img--rounded
    } //.float-card__img

    &__title {
        margin-bottom: 30px;
        color: $color-text--darkest;
        text-align: center;

        @include min(md) {
            font-size: 25px;
        }
    } //.float-card__title

    &__price {
        justify-content: center;
        color: $color-accent;
        font-size: 22px;
        font-weight: 600;
        font-weight: 500;
        @include flexCenter;

        @include min(md) {
            font-size: 30px;
        }

        & + .float-card__note {
            margin-top: 0;
            margin-bottom: 20px;
        }

        .small {
            margin-left: 3px;

            font-size: 16px;

            @include min(md) {
                font-size: 20px;
            }
        }
    } //.float-card__price

    &__note {
        margin: 10px auto 5px;
        color: $color-text--light;
        font-size: 13px;
        text-align: center;
    } //.float-card__note

    &__desc {
        color: $color-text--darkest;
        text-align: center;
    } //.float-card__desc

    &__btn {
        width: 100%;
        margin-top: 25px;
    } //.float-card__btn
} //.float-card

.video {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    @include cover;

    @include min(md) {
        margin-left: 0;
    }

    &__btn {
        border: 0;
        outline: none;
        background-color: transparent;
        @include centre;

        &::before {
            content: '';
            z-index: -1;
            display: block;
            width: 20px;
            height: 20px;
            background-color: #fff;
            @include centre;
        }

        i {
            color: #f00;
            font-size: 50px;
        }
    } //.video__btn

    &__wrapper {
        @include min(md) {
            width: 1000px;
            margin: auto;
        }
    } //.registration .video__wrapper

    iframe,
    .youtube {
        @include coverdiv;
    } //.video
} //.video

.video-mockup {
    position: relative;
    width: 75%;
    max-width: 340px;
    margin: -90px auto 0;

    @include screen(1024, 1200) {
        margin: -140px auto 0;
    }

    &__video {
        position: absolute;
        top: 0;
        left: 0;
        padding: 22% 9% 9%;
    } //.video-mockup__mockup

    &__play-btn {
        position: absolute;
        top: 55%;
        left: 50%;
        padding: 23px;
        transform: translate(-50%, -50%) rotate(90deg);
        border: 3px solid #4a484b;
        border-radius: 50%;
        outline: none;
        background-color: transparent;
        cursor: pointer;

        &:hover {
            background-color: rgba(0, 0, 0, 0.06);
            cursor: pointer;
        }

        &:active {
            border-color: #fff;

            i {
                color: #fff;
            }
        }

        i {
            color: #4a484b;
            font-size: 20px;
        }
    } //.video-mockup__play-btn

    video {
        width: 100%;
        height: 100%;
        border: 3px solid $color-border--lightest;
        border-radius: $border-radius;
    }
} //.video-mockup

.client-logo {
    margin: auto;

    @include sm-block() {
        flex-basis: 30%;

        img {
            width: 55%;
            margin: auto auto 30px;
        }
    }

    @include max(xs) {
        img {
            width: 90%;
            margin: 0;
        }
    }

    &__wrap {
        margin-top: 30px;
        @include flexCenter;

        @include sm-block() {
            flex-wrap: wrap;
            margin-top: 0;
        }
    }
} //.client-logo

.screen {
    &--adv {
        .icon-list {
            @include row-flex;

            &__item {
                @include col;
                @include size(4);
                @include size-xs(12);

                @include xs-block() {
                    flex-direction: column;
                }
            } //.screen--adv .icon-list__item

            &__icon {
                @include xs-block() {
                    width: 70px;
                    height: 70px;
                    margin-right: 0;
                    margin-bottom: 20px;
                }
            } //.screen--adv .icon-list__icon

            &__desc {
                @include xs-block() {
                    text-align: center;
                }
            } //.screen--adv .icon-list__desc

            &__title {
                font-size: 20px;
            } //.screen--adv .icon-list__title

            &__sub {
                font-size: 18px;
            } //.screen--adv .icon-list__sub
        } //.screen--adv .icon-list
    } //.screen--adv
} //.screen
