.review {
    &__img {
        width: 50px;
        height: 50px;
        margin-top: -7px;
        margin-left: 15px;
        float: right;
        overflow: hidden;
        border-radius: 50%;
    } //.review__img

    &__sub {
        margin-bottom: 55px;
        color: $color-text--light;
        font-size: 16px;
        font-weight: 300;
        line-height: 1.7;
    } //.review__sub

    &__name {
        color: $color-text--darkest;
        font-size: 14px;
        font-weight: 500;
        text-align: right;
    } //.review__author

    &__company {
        font-size: 14px;
        line-height: 1.5;
        text-align: right;
    } //.review__company
} //.review
